.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


h1{
  text-align: center;
  font-size: 3rem;
}


button{
  padding: 10px 20px;
  background-color: black;
  color: white;
  font-size: 1.2rem;
  cursor: pointer;
margin-left: 45vw;
}
input{
margin-left: 45vw;
margin-bottom: 20px;
margin-top: 20px;
}

img{
  width: 700px;
  height: 500px;
  margin:20px 0 30px 33vw;
  display: block;
}
video{
  width: 880px;
  height: 500px;
  margin:20px 0 30px 26vw;
  display: block;
}